<template xmlns:v-lazy="http://www.w3.org/1999/xhtml">
    <div class="views detail">
        <navigate :title="navigateTitle" position="absolute"/>
        <div class="scroll-wrapper">
            <div class="header">
                <!--<div class="picture-wrapper border-1px">-->
                <!--<div class="picture" v-lazy:background-image="$images + detail.icon"></div>-->
                <!--</div>-->
                <div class="info">
                    <div class="name" v-if="isNull(detail.name)">{{detail.name}}</div>
                    <div v-if="isNull(detail.bridge_category)">
                        {{detail.type | formatElementName}}类型: {{detail.bridge_category}}
                    </div>
                    <div v-if="isNull(detail.markType)">
                        标志类型: {{detail.markType}}
                    </div>
                    <div v-if="isNull(detail.isShared)">
                        标注类型: {{detail.isShared ? '共享' : '私有'}}
                    </div>
                    <div v-if="isNull(detail.mmsi)">MMSI: {{detail.mmsi}}</div>
                    <div v-if="isNull(detail.beacon_number)">航标编号: {{detail.beacon_number}}</div>
                    <div v-if="isNull(detail.manage_department)">管理部门: {{detail.manage_department}}</div>
                </div>
            </div>
            <div class="button-wrapper border-1px-t">
                <mu-button class="button border-1px-r" @click="handlerLocation" flat>
                    <mu-icon style="margin-right: .25em" value="location_on" size="16"/>
                    定位
                </mu-button>
                <mu-button class="button" :style="`color: ${collectionColor}`"
                           @click="handlerCollection"
                           v-if="visibleCollection"
                           flat>
                    <mu-icon style="margin-right: .25em" :color="collectionColor"
                             :value="detail.isStar ? 'favorite' : 'favorite_border'"
                             size="18"/>
                    {{detail.isStar ? '取消收藏' : '收藏'}}
                </mu-button>
            </div>
            <div style="height: 10px; background-color: #EFEFEF"></div>
            <div class="data-wrapper">
                <div class="border-1px-b" style="padding-bottom: 15px; font-size: 16px">基本信息</div>

                <!-- 详细内容， 由 contrast 对象生成 -->
                <template v-for="(item, key, index) in contrast[typeId]">
                    <div class="item">{{contrast[typeId][key]}}: {{detail[key]}}</div>
                </template>
                <!-- 详细内容， 由 contrast 对象生成 -->
            </div>
        </div>
    </div>
</template>

<script>
    import Navigate from "../../base/navigate/navigate"
    import Elements from '../../common/utils/elements'
    import NoSuccess from "../../components/no-success/no-success"
    import Toast from 'muse-ui-toast'
    import {saveOrUpdateIBoat, deleteIBoatByBoatId, queryILabelDetail} from "../../api/personal"
    import {getNavigationDetail} from "../../api/navigational"
    import {MapMixins} from "../../common/js/mixins"
    import {userMixins, layerMixins} from "../../common/mixins"

    const contrast = {
        // 服务点
        '013008': {
            describe: "描述"
        },
        // 服务点
        '013009': {
            describe: "描述"
        },
        // 浮码头
        '013004': {
            dockNumber: "码头编号",
            dockType: "码头类型",
            dockNature: "码头性质",
            mainUses: "码头主要用途",
            shorelineApproval: "岸线批文",
            manageDepartment: "管理单位",
            structureType: "结构形式",
            berthingCapacity: "码头靠泊能力",
            designYear: "设计使用年限",
            buildState: "建设状态",
            ashore: "岸别"
        },
        // 固定码头
        '013003': {
            dockNumber: "码头编号",
            dockType: "码头类型",
            dockNature: "码头性质",
            mainUses: "码头主要用途",
            shorelineApproval: "岸线批文",
            manageDepartment: "管理单位",
            structureType: "结构形式",
            berthingCapacity: "码头靠泊能力",
            designYear: "设计使用年限",
            buildState: "建设状态",
            ashore: "岸别"
        },
        // 线缆
        '013016': {
            cableType: "线缆类别",
            burialDepth: "埋设深度(米)",
            buildState: "建设状态",
            manageDepartment: "管理单位",
            remark: "备注",
            objectId: "对应空间数据库",
            cableLength: "线缆长度(米)",
            safeSpacing: "线缆安全间距",
            biddingSituation: "管道设标情况",
            cableStatus: "线缆状况",
            cableState: "线缆状态"
        },
        // 禁锚区
        '013013': {
            describe: "描述"
        },
        // 船闸
        '013005': {
            crossLevel: "通过等级",
            lockChamber: "闸室",
            lockLength: "有效长度(米)",
            lockWidth: "有效宽度(米)",
            lockDepth: "船闸门槛水深(米)",
            crossLogin: "通过登录",
            buildYear: "建设年份",
            manageDepartment: "管理单位",
            numberCode: "线数代码",
            doorWidth: "闸首口门宽",
            minDepth: "闸槛最小水深"
        },
        // 航标
        '013007': {
            // markNumber: "航标编号",
            // naviMarkType: "航标类型",
            // markStatus: "航标状态",
            markType: "标志类型",
            // designType: "设计类型",
            ashore: "岸别",
            setPosition: "设置位置",
            bsColour: "标身颜色",
            bsPattern: "标身图案",
            dbShape: "顶标形状",
            dbColour: "顶标颜色",
            btColour: "标体颜色",
            btShape: "标体形状",
            lightColour: "灯色",
            lightCharacter: "灯质",
            buildingProperty: "建筑结构性质",
            // hbycykhm: "航标遥测遥控号码",
            flashCycle: "闪光周期",
            // rotateAngle: "旋转角度",
            signalSequence: "信号序列",
            // minScale: "最小比例尺",
            // bzjggg: "标志结构规格",
            // buildState: "建设状态",
            // manageCategory: "管理类别",
            manageDepartment: "管理单位"
            // maintainDepartment: "维护单位"
        },
        // 锚地
        '013012': {
            name: "锚地名称",
            describe: "描述",
            roadsteadType: "锚地类型",
            berthMode: "停泊方式",
            waterDepth: "锚泊区水深",
            buoyNumber: "浮筒个数",
            mooringAbility: "系泊能力",
            mooringNumber: "锚泊艘数",
            bottomAbility: "锚泊区底质",
            area: "锚泊区面积"
        },
        // 浅滩
        '013015': {
            shoalLength: "浅滩长度",
            shoalType: "浅滩类型",
            describe: "描述"
        },
        // 水位站
        '013010': {
            manageDepartment: "管理部门",
            reportTime:"上报时间",
            waterLevel:"水位值(米)",
            describe: "描述"
        },
        // 船舶
        '013001': {
            boatWidth: "船宽(米)",
            boatHeight: "船高(米)",
            boatLength: "船长(米)",
            draught: "设计吃水(米)",
            year: "出厂年度",
            mmsi: "MMSI",
            callNumber: "呼号",
            imo: "IMO",
            speed: "船速(节)",
            heading: "航向(度)",
            manageUnit: "单位",
            boatType: "船舶类型",
            buildingPlant: "船舶建造场",
            designUnit: "设计单位",
            department: "组织机构",
            totalTon: "总吨",
            netTon: "净吨",
            fixedPerson: "定员",
            mTime:"更新时间"
        },
        // 桥梁
        '013002': {
            bridgeType: "桥梁类型",
            bridgeStructure: "桥梁结构形式",
            bridgeWidth: "桥面宽度(米)",
            bridgeLength: "桥梁长度(米)",
            holesNumber: "通航孔数(个)",
            pirNumber: "桥墩个数(个)",
            // bridgeState: "桥梁状态",
            highScale: "净空高尺度(米)",
            widthScale: "净宽尺度(米)",
            updateTime:"更新时间"
        },
        // 危险区域
        '013014': {
            dangerareaType: "危险区域类型",
            location: "位置",
            remark: "备注"
        },
        // 天气
        '013011': {
            province: "省份",
            city: "城市",
            weatherDescribe: "天气描述",
            temperature: "温度(摄氏度)",
            bodyTemperature: "体表温度(摄氏度)",
            windDirection: "风向",
            windSpeed: "风速(公里/小时)",
            windScale: "风力等级",
            humidity: "湿度",
            pa: "压强(百帕)",
            precipitation: "降水量(毫米)",
            visibility: "能见度(公里)",
            updateTime: "更新时间",
            dataSources: "数据源"
        },
        //标注详细
        '001001': {
            detail: "描述"
        }
    }

    export default {
        name: "detail",
        mixins: [MapMixins, userMixins, layerMixins],
        data() {
            return {
                detail: {},
                contrast
            }
        },
        props: {
            type: {
                type: String,
                default: ''
            },
            id: {
                type: [String, Number],
                default: ''
            }
        },
        computed: {
            /***
             * 获取 type ID
             * @return {*}
             */
            typeId() {
                return this.$route.query.typeId
            },
            /***
             * 导航栏标题
             * @return {string}
             */
            navigateTitle() {
                if (this.typeId === '001001') {
                    return '标注详情'
                }

                const result = Elements.find(item => {
                    return item.id === this.typeId
                })

                return result ? result.name + '详情' : '详情'
            },
            /***
             * 收藏按钮显示隐藏
             * @return {boolean}
             */
            visibleCollection() {
                return this.typeId === '013001' && !this.$route.query.hideCollection
            },
            collectionColor() {
                return this.detail.isStar ? "#e91e63" : "#333"
            }
        },
        activated() {
            this.detail = {}

            this.getNavigationDetail()
        },
        methods: {
            // 定位
            handlerLocation() {
                this.$toast({
                    message: '定位中 ...',
                    duration: 0
                })

                this.setCenter(this.detail.longitude, this.detail.latitude).then(res => {
                    const layer = this.getLayer(this.typeId)

                    if (layer && !layer.status) {
                        Toast.message({
                            position: 'bottom',
                            message: `请在图层控制面板打开${layer.name}专题图图层`,
                            time: 2500
                        })
                    }

                    this.$router.push('/')
                }).catch(err => {
                    this.$toast({
                        message: err
                    })
                })
            },
            // 收藏
            handlerCollection() {
                if (!this.isLogin) return this.$toast('您需要登录才能收藏船舶! ')

                this.detail.isStar = !this.detail.isStar

                if (this.detail.isStar) {
                    saveOrUpdateIBoat(this.id).then(res => {
                        this.$toast('收藏成功')
                    })
                } else {
                    deleteIBoatByBoatId(this.id).then(res => {
                        this.$toast('取消成功')
                    })
                }
            },
            // 获取详情
            getNavigationDetail() {
                if (this.typeId === '001001') {
                    queryILabelDetail(this.id).then(res => {
                        res.status = ''
                        res.name = res.title

                        this.detail = res
                    })

                    return
                }


                const params = {
                    typeId: this.type,
                    id: this.id
                }

                getNavigationDetail(params).then(res => {
                    this.detail = res
                })
            },
            isNull(obj) {
                return !(typeof obj === 'undefined' || obj === null)
            },
            isUndefined(obj) {
                return !(typeof obj === 'undefined')
            }
        },
        filters: {
            formatElementName(id) {
                const result = Elements.find(item => {
                    return item.id === id
                })

                return result ? result.name : ''
            }
        },
        components: {
            NoSuccess,
            Navigate
        }
    }
</script>

<style lang="stylus" scoped>
    @import "../../common/stylus/variable.styl"

    .detail
        padding-top 46px
        width 100%
        height 100%
        background-color #fff
        overflow hidden
        .scroll-wrapper
            width 100%
            height 100%
            .header
                display flex
                padding 15px
                .picture-wrapper
                    margin-right 15px
                    width 23%
                    .picture
                        padding-top 80%
                        width 100%
                        height 0
                        background-color #efefef
                        background-position center center
                        background-size cover
                .info
                    flex 1
                    min-height 60px
                    font-size $font-size-ml
                    text-align center
                    .name
                        color #3899D7
                        line-height 1.74625
                        font-weight 500
                    > *
                        margin-bottom 10px
                        &:last-child
                            margin-bottom 0
            .button-wrapper
                display flex
                align-items center
                .button
                    flex 1
                    height 40px
                    line-height 40px
            .data-wrapper
                padding 15px
                .item
                    position relative
                    padding-left 20px
                    height 38px
                    line-height 38px
                    &:before
                        content ''
                        position absolute
                        top 16px
                        left 7px
                        display block
                        width 6px
                        height 6px
                        border-radius 50%
                        background-color #3899D7
</style>



